.header{position:fixed;max-width:300px;width:100%;height:100vh;background:#252526}.header .logo{position:absolute;margin-top:-100px;margin-left:0px;width:300px;height:300px}.header nav ul{padding:200px 30px}.header nav ul li{padding:10px 0px;font-size:18px;font-weight:700;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif;color:#5f5f5f}.header nav ul li a{color:#5f5f5f}.header nav ul li span{font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Open Sans","Helvetica Neue",sans-serif;background-image:linear-gradient(#3495fd, #ff00b3);color:#fff;padding:5px;border-radius:10px;margin-left:5px}.header nav ul li .icon{margin-right:8px;margin-bottom:-8px;color:#3495fd}.header nav a:hover{padding:10px;cursor:pointer;transition:.3s;color:#fff;border-radius:10px;background:#1a1a1a}.header nav a:focus{padding:10px;cursor:pointer;transition:.3s;color:#fff;border-radius:10px;background:#1a1a1a}.header .links{margin-left:30px;margin-top:-10px}.header .links .url{margin-bottom:-4px}.header .links .inst{margin-left:15px;cursor:pointer}.header .links .tube{cursor:pointer;margin-bottom:-4px;margin-left:10px}.header .links .mail{cursor:pointer;margin-left:9px;margin-bottom:-1px}.header .links .face{margin-left:12px}.header .links .gmail{cursor:pointer;margin-left:12px;margin-bottom:-4px}#burger,[for=burger]{display:none}@media all and (max-width: 1445px){.header{height:110%}.header .links{margin-top:30px}}@media all and (max-width: 770px){.header{background:transparent;display:block}.header .logo{display:none}.header nav{display:none;position:fixed;background:#252526;width:500px;height:100%;user-select:none}.header nav ul{margin:0;padding:30px;list-style-type:none}.header .links{display:none}}[for=burger]{display:block;position:fixed;width:30px;text-align:center;right:20px;top:10px;color:#fff}[for=burger]::before{content:"☰"}#burger:checked+label+nav{display:block;animation:show .5s ease}#burger:checked+[for=burger]::before{content:"⮾"}@keyframes show{0%{opacity:0}100%{opacity:1}}@media all and (max-width: 376px){.header nav{display:none;position:fixed;background:#252526;width:280px;height:100%;user-select:none}}@media all and (max-width: 1025px){.header .logo{width:300px;height:250px}}